import './Home.css'
import{ReactComponent as CSSLogo} from "./assets/css.svg"
import{ReactComponent as JavaLogo} from "./assets/java.svg"
import{ReactComponent as ReactLogo} from "./assets/react.svg";
import{ReactComponent as HtmlLogo} from "./assets/icons8-html-5 (1).svg";
import{ReactComponent as JSLogo} from "./assets/icons8-javascript.svg";
function Home() {
    return(
    <div className="Home">
     <h1 className="initial-header">
      William Davis
    </h1>
     <CSSLogo className = "HomeIcon"/>
     <JSLogo className = "HomeIcon"/>
     <JavaLogo className = "HomeIcon"/>
     <HtmlLogo className = "HomeIcon"/>
     <ReactLogo className = "HomeIcon"/>
     <p className='home-text'> 
     I'm a student aspiring to be a developer.

     </p>
     <p className='home-text'> 
      Check out some of the work that I've done!
     </p>
    </div>
  )
}
export default Home;